.Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}@keyframes ant {
    0% {
        opacity: 1;
        right: -20px;
    }
    50% {
        opacity: 1;
        right: 80%;
        transform: rotate(800deg);
    }
    100% {
        right: 100%;
    }
}

@keyframes img-1 {
    0% {
        transform: translateX(736px);
        opacity: 0;
    }
    100% {
        transform: translateX(0px);
        opacity: 1;
    }
}

// img-4
@keyframes img-4 {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}

// about
@keyframes about {
    0% {
        transform: scale(.7);
    }
    100% {
        transform: scale(1.2);
    }
}

// choose
@keyframes choose {
    0% {
        transform: scale(.7);
    }
    100% {
        transform: scale(1);
    }
}

// img-5
@keyframes img-5 {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes opacit {
    0% {
        transform: translate(-36px, 9px);
        opacity: 0;
    }
    100% {
        transform: translate(0, 0);
        opacity: 1;
    }
}