@import "main";
// End Of Import

// Start Header
// headder
header {
    height: 1000px;
    position: relative;
    // button
    .button {
        position: absolute;
        bottom: 0;
        left: 0;
        overflow: hidden;
        width: 100%;
        z-index: -1;
    }
    // plus
    .plus {
        position: absolute;
        bottom: 20px;
        left: -110px;
        width: 100%;
        overflow: hidden;
    }
    // .two
    .two {
        position: absolute;
        top: 140px;
        left: -70px;
        width: 100%;
        overflow: hidden;
    }
    // navbar
    .navbar {
        padding: 50px 0;
        h2 {
            font-family: "Poppins-Bold";
            color: #fcae61;
            font-size: 30px;
            span {
                color: #242f51;
            }
        }
        // .section-one
        .slidebar {
            @extend %flex;
            
            ul {
                @extend %flex;
                li {
                    margin-left: 30px;
                    @include Sm{
                    
                        margin-left: 0;
                        margin-bottom: 30px;
                    }
                    a {
                        font-family: "Poppins-Regular";
                        font-size: 16px;
                        color: #242f51;
                    }
                }
            }
            // search
            .close-nav{
                cursor: pointer;
            }
            .search-col {
                display: none;
                @include Sm{
                    position: absolute;
                    top: 0;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 0px 30px;
                    height: 80px;
                }
                
            }
            
            // .try 
            .try {
                margin-left: 70px;
                @include Sm {
                    margin-left: 0;
                }
                button {
                    background-color: #2500f9;
                    width: 170px;
                    height: 50px;
                    border-radius: 30px;
                    a {
                        color: #fff;
                        font-size: 16px;
                        font-family: "Poppins-Bold";
                        @include Sm {
                            font-size: 14px;
                        }
                    }
                }
            }
        }
        // toggle
        .toggle {
            display: none;
            @include Sm {
               display: block;
               cursor: pointer;
               i{
                font-size: 25px;
               }
            }
        }
    }
    
    .slidebar.active{
        transform: scaleX(1);
        display: block;
        
    } 
    .slidebar.active ul{
        display: block;
    }
    .slidebar{
        @include Sm{
            position: fixed;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            z-index: 9999;
            transform-origin: right;
            transform: scaleX(0);
            transition: all .5s ease-in-out;
            background-color: #fff;
            }
            ul{
                @include Sm{
                    display: block;
                    width: 300px;
                    margin: auto;
                    margin-top: 100px;
                    -webkit-transform: translateX(0);
                    transform: translateX(0);
                }
            }
    }
    
    
    // navbar
    // section-one
    .section-one {
        position: relative;
        background-image: url(../images/layer-5.png);
        background-repeat: no-repeat;
        background-position: bottom;
        overflow: hidden;
        // img
        .pt {
            img {
                position: absolute;
                opacity: 0;
                right: -10%;
            }
            img:nth-of-type(1) {
                top: 5px;
                animation: ant 50s 1s linear infinite alternate;
            }
            img:nth-of-type(2) {
                top: 10%;
                animation: ant 60s 10s linear infinite alternate;
            }
            img:nth-of-type(3) {
                top: -5%;
                animation: ant 60s 20s linear infinite alternate;
            }
            img:nth-of-type(4) {
                top: 40%;
                animation: ant 50s 4s linear infinite alternate;
            }
            img:nth-of-type(5) {
                top: 50%;
                animation: ant 40s 10s linear infinite alternate;
            }
            img:nth-of-type(6) {
                top: 80%;
                animation: ant 50s 15s linear infinite alternate;
            }
            img:nth-of-type(7) {
                top: 60%;
                animation: ant 50s linear infinite alternate;
            }
        }
        // img-one
        .img {
            opacity: 0;
            animation: img-1 1s 1.7s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards;
        }
    }
    // .contant
    .contan {
        .row {
            align-items: center;
        }
        .hash {
            background-color: #e2eaed;
            display: inline-block;
            border-radius: 30px;
            padding-right: 30px;
            padding: 5px 50px 5px 0;
            i {
                background-color: #feba4e;
                color: #fff;
                border-radius: 50%;
                padding: 10px;
            }
        }
        span {
            font-size: 18px;
            margin-left: 15px;
            font-family: "Poppins-Regular";
            color: #242f51;
        }
    }
    h1 {
        margin-top: 25px;
        color: #242f51;
        font-size: 60px;
        font-family: "Poppins-SemiBold";
        @include Md {
            font-size: 50px;
        }
    }
    p {
        margin-top: 40px;
        line-height: 1.7;
        color: #616368;
        font-size: 20px;
        font-family: "Poppins-Regular";
        @include Md {
            font-size: 16px;
        }
    }
    // .link 
    .link {
        margin-top: 40px;
        @extend %flex;
        .try {
            margin-top: 0;
            button {
                background-color: #2500f9;
                width: 170px;
                height: 50px;
                border-radius: 30px;
                a {
                    color: #fff;
                    font-size: 16px;
                    font-family: "Poppins-Bold";
                    margin: 0;
                }
            }
        }
        a {
            margin-left: 20px;
            color: #2500f9;
            font-size: 16px;
            font-family: "Poppins-Regular";
        }
    }
}

