html {
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        // background-color: $color-sec;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        // background-color: $color-prim;
    }

    // overflow-y: hidden;
    // overflow-x: hidden;

    scrollbar-width: thin;
    // scrollbar-color: rgba($color-prim, 1) rgba($color-sec, 1);
}

body {
    font-family: "reg", sans-serif;

    @include Sm {
        text-align: center;
    }
}

* {
    font-family: inherit;
}

input,
a,
button {
    font-family: "reg", sans-serif;

    &:focus {
        outline: none;
        text-decoration: none;
    }

    &:hover {
        text-decoration: none;
    }
}

button {
    cursor: pointer;

    &:focus,
    &:active {
        outline: none;
        box-shadow: none;
    }
}

ul.res {
    padding: 0;
    margin: 0;
    list-style: none;
}
